.webinar {
	background: url(../img/bg-2.jpg) no-repeat center;
	background-size: cover;
	& > * {
		color: @white;
	}

	.flex-container {
		flex-wrap: nowrap;
		justify-content: space-between;
	}
}

.webinar_wrapper {
	flex: 0 1 400px;
	padding: 74px 0;
	h2 {
		margin-left: -42px;
	}
	ul {
		li {
			position: relative;
			padding-top: 35px;
			font-size: 14px;
			opacity: 0.8;
			&:before {
				position: absolute;
				content: "";
				    width: 32px;
    				height: 34px;
    				top: 28px;
    				left: -40px;
				z-index: 1;

			}
			&:first-child:before {
				background: url(../img/svg/5.svg) no-repeat left;
			}
			&:nth-child(2):before {
				background: url(../img/svg/3.svg) no-repeat left;
			}
			&:nth-child(3):before {
				background: url(../img/svg/4.svg) no-repeat left;
			}
			&:nth-child(4):before {
				background: url(../img/svg/1.svg) no-repeat left;
			}
			&:last-child:before {
				background: url(../img/svg/2.svg) no-repeat left;
			}
		} 
	}
}

.empty_block {
	height: 1px;
	width: 70px;
}


.about_company {
	.flex-container {
		h2 {
			margin: 0 auto;
			margin-bottom: 10px;
		}
		margin-top: 80px;
		margin-bottom: 72px;
	}
}


.about_wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
	.about_card {
		width: 25%;
		min-width: 250px;
		padding-top: 32px;
		height: 115px;
		vertical-align: middle;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			margin: 0 auto;	
			filter: grayscale(1);
			transition: all 0.3s ease;
			cursor: pointer;
			&:hover {
				filter: grayscale(0);
			}	
		}
	}
}