.thansk{
	
	
	height: 100vh;
	position: relative;
	& > *{
		position: relative;
		z-index: 2;
	}
	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		background: url(../img/thanks.jpg) no-repeat 30% 40%;
		background-size: cover;
	}
	.center{
		height: 100%;
		.flex-container{
			height: 100%;
			flex-direction: column;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: center;
	}
}
}


.thanks_wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
}


 .thanks_btn {
        cursor:pointer;
            background: linear-gradient(to right, rgba(246,41,12,1) 0%, rgba(32,139,109,1) 0%, rgba(25,119,140,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f6290c', endColorstr='#19778c', GradientType=1 ); 
            &:hover {
                background: linear-gradient(to left, rgba(246,41,12,1) 0%, rgba(32,139,109,1) 0%, rgba(25,119,140,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#19778c', endColorstr='#f6290c', GradientType=1 ); 
            }
            margin-top: 20px;
            width: 38%;
            min-width: 268px;
            padding: 20px 73px;
            border-radius: 35px;
            color: @white;

    }

h2{
	font-size: 30px;
	padding-top: 30px;
	display: block;
	margin-bottom: 20px;
}

p {
	display: block;
	margin-top: 20px !important;
	a{
		text-decoration: underline;
		color: #4383c5;
	}
}    


@media screen and (max-width: 690px) {
	.thanks_wrapper {
		max-width: 580px;
	}
}

@media screen and (max-width: 550px) {
	.thanks_btn{
		margin: 80px auto 0;
	}

	.thanks_wrapper{
		background: transparent !important;
	}
}

@media screen and (max-height: 1012px) {
	.thanks_wrapper{
	background: rgba(255,255,255,0.6);
	border-radius: 25px;
	padding: 10px;
	}
}