.container {
    width: 100%;
    text-align: center;
    li {
  display: inline-block;
  list-style-type: none;
  padding: 0 3%;
  text-transform: capitalize;
  text-align: center;
    }

li span {
  display: block;
  font-size: 48px;
  color: #ff15c1;
  font-family: @font_;
  text-align: center;
}
}


.only_form {
    background: #f2f2f2;
    .flex-container {
        flex-direction: column;
        padding: 80px 0;
    }

    .only_form-title {
        margin-bottom: 40px;
    }
}


.numbers-form-wrapper {
    max-width:  500px;
    width: 100%;
    min-width: 275px;
    padding: 30px 40px;
    text-align: center;
    position: relative;
    height: 100%;
    margin: auto;
    vertical-align: middle;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.numbers-form-wrapper .text-field {
    margin-bottom: 30px;
}


.numbers-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 100%;
    span {
        align-self: flex-start;
        margin-top: 20px;
    }
}


.input-wrapper {
    position: relative;
        width: 100%;
        height: 52px;
        top: 0;
        left: 0;
        background: linear-gradient(to left, rgba(243,109,20,1) 0%, rgba(241,111,92,1) 0%, rgba(246,41,12,1) 0%, rgba(250,24,193,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa18c1', endColorstr='#f36d14', GradientType=1 );
            z-index: 10;
        & > input {
            position: absolute;
            top: 1px;
            left: 1px;
            padding: 25px 30px;
            width: 99.5%;
            font-size: 16px;
            height: 95%;
        }
    }


    .numbers_btn {
        cursor:pointer;
            background: linear-gradient(to right, rgba(246,41,12,1) 0%, rgba(32,139,109,1) 0%, rgba(25,119,140,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f6290c', endColorstr='#19778c', GradientType=1 ); 
            &:hover {
                background: linear-gradient(to left, rgba(246,41,12,1) 0%, rgba(32,139,109,1) 0%, rgba(25,119,140,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#19778c', endColorstr='#f6290c', GradientType=1 ); 
            }
            margin-top: 35px;
            width: 100%;
            padding: 20px 35px;
            border-radius: 35px;
            color: @white;

    }


    .intl-tel-input.allow-dropdown input, .intl-tel-input.allow-dropdown input[type=text], .intl-tel-input.allow-dropdown input[type=tel], .intl-tel-input.separate-dial-code input, .intl-tel-input.separate-dial-code input[type=text], .intl-tel-input.separate-dial-code input[type=tel] {
       
    margin-left: 0;
    width: 100% !important;
        padding: 17px 6px 15px 74px;
        margin-top: 1px !important;

    }


    .intl-tel-input.allow-dropdown {
        width: 99.5%;
        height: 99%;
    }


    .intl-tel-input .selected-flag {
        padding: 0 6px 0 35px;
    }



