@import "node_modules/normalize.less/normalize.less";
@import "_variables.less";
@import "_third.less";
@import "_timer.less";
@import "_thanks.less";

*, *:after, *:before {
	box-sizing: border-box;
}
html, section, main, footer{
	overflow-x: hidden;
}

body {
	font-family: @font_;
	font-size: 16px;
	font-weight: @regular;
	overflow-x: hidden;
	
}


ul, li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	color: @black;
}

img {
	display: block;
	max-width: 100%;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {
	text-transform: uppercase;
	font-size: 30px;
	font-size: @bold;
}

input {
	border: none;
	outline: none;
}

a, input[class$="_btn"] {
	font-size: 16px;
}


.center {
	max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.flex-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}

.hidden() {
	display: none;
}

.mt() {
	margin-top:120px;
}
.header {
	height: 100vh;
	background: url(../img/bg.jpg) no-repeat center;
	background-size: cover;
	background-position: 59%;
	min-height: 680px !important;
	position: relative;
	

	.center, .flex-container {
		height: 100vh;
		min-height: 680px !important;

	}
	.center {
		min-height: 680px !important;
		
	}
	.flex-container {
		flex-direction: column;
		padding-top: 20px;
		min-height: 680px !important;
	}
}

.bottom_header {
	position: relative;
}

.header_top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	.header_logo {
		cursor: pointer;
	}
	.header_masterclass {
		padding: 12px 20px;
		background: @green;
		color: @white;
		border-bottom: 4px solid @pink;
		font-weight: @bold;
	}
}



.header_middle {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	.header_title{
		h1{
			color: @white;
			font-size: 36px;
			font-weight: @bold;
			text-transform: uppercase;
		}
		p{
			.hidden();
			color: @white;
			position: relative;
			font-size: 18px;
			font-weight: @bold;
			top:-24px;
			left:20px;
			z-index: 2;
			&:before {
				content: "";
				position: absolute;
				width: 60px;
				height: 60px;
				background: linear-gradient(to right,#ff7117 0,#ff7017 0,#fa19c2 100%);
				    top: -29px;
   				 left: -14px;
				z-index: -1;
				border-radius: 50%;


			}
		}
	}
	.header_button {
		margin-top: 60px;
		a {
			padding: 20px 35px;
			border-radius: 35px;
background: linear-gradient(to right, rgba(255,113,23,1) 0%, rgba(255,112,23,1) 0%, rgba(250,25,194,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff7117', endColorstr='#fa19c2', GradientType=1 );
			transition: all 0.3s linear;
			&:hover {
background: linear-gradient(to left, rgba(255,113,23,1) 0%, rgba(255,112,23,1) 0%, rgba(250,25,194,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa19c2', endColorstr='#ff7117', GradientType=1 );
			}
			span {
				color: @white;
			}
		}
	}
}



.header_middle-right {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-end;
	flex-basis: 400px;
	margin-top: 15%;
	.header_img {
		position: relative;
		width: 125px;
		height: 125px;
		margin-right: 23px;

	}
	p {
		color: @white;
		font-size: 24px;
		font-weight: @bold;
		margin-top: -25px;
		position: absolute;
		top: 127px;
    	left: -115px;
		display: block;
		width: 350%;
	}
}

.bottom_header {
	width: 100%;
}

.header_bottom {

	padding: 15px 25px;
		position: relative;
	background: @green;
	display: flex;
	justify-content: space-around;
	align-items: stretch;
	flex-wrap: nowrap;
	bottom: -40px;
	z-index: 5;
	width: 95%;
	max-width: 1170px;
	border-bottom: 4px solid @pink;
	& > div {
		display: flex;
		justify-content: space-between;
		flex-wrap: nowrap;
		align-items: center;
		margin-right: 30px;
	}

	& >div:last-child{
		margin-right: 0;

	}
	& > div> img {
		width: 32px;
		height: 32px;


	}
	
	.header_bottom-bonus {
		color: @white;
		font-weight: bold;
		font-size: 24px;
	}

	.header_bottom-video, .header_bottom-pdf {
		display: flex;
		flex: 0 1 330px;
		color: @white;
		word-spacing: 0.2px;
		img {
			margin-right: 20px;
		}
	}
}

.master_class {
	overflow-y: hidden;
	.flex-container{
		flex-direction: column;
		.mt();
		margin-bottom: 82px;
	}
}

.masterclass_content {
	.masterclass_cardswrapper {
		display: flex;
		justify-content: center;
		align-items: stretch;
		flex-wrap: wrap;
		.masterclass_card {
			flex: 0 1 350px;
			height: auto;	
			
		}
	}
}


.header_title {
	position: relative;
	.header_masterclass {
		padding: 11px 20px;
		background: @green;
		color: @white;
		border-bottom: 4px solid @pink;
		font-weight: @bold;
    position: absolute;
    top: -17px;
