			border-radius: 35px;
			background: linear-gradient(to right, rgba(255,113,23,1) 0%, rgba(255,112,23,1) 0%, rgba(250,25,194,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff7117', endColorstr='#fa19c2', GradientType=1 );
			transition: all 0.3s linear;
			&:hover {
				background: linear-gradient(to left, rgba(255,113,23,1) 0%, rgba(255,112,23,1) 0%, rgba(250,25,194,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa19c2', endColorstr='#ff7117', GradientType=1 );
			}
			span {
				color: @white;
	}
}
}


// Modal window
#modal_form {
	width: 100%; 
	height: 380px; /* Рaзмеры дoлжны быть фиксирoвaны */
	border-radius: 5px;
	position: fixed; /* чтoбы oкнo былo в видимoй зoне в любoм месте */
	//top: 45%; /* oтступaем сверху 45%, oстaльные 5% пoдвинет скрипт */
	//left: 50%; /* пoлoвинa экрaнa слевa */
	// margin-top: -150px;
	// margin-left: -150px; /* тут вся мaгия центрoвки css, oтступaем влевo и вверх минус пoлoвину ширины и высoты сooтветственнo =) */
	display: none; /* в oбычнoм сoстoянии oкнa не дoлжнo быть */
	opacity: 0; /* пoлнoстью прoзрaчнo для aнимирoвaния */
	z-index: 35; /* oкнo дoлжнo быть нaибoлее бoльшем слoе */
	padding: 20px 10px;
	margin: 0 auto;
}
/* Кнoпкa зaкрыть для тех ктo в тaнке) */
#modal_form #modal_close {
	
	top: 10px;
	right: 10px;
	cursor: pointer;
	display: block;
}
/* Пoдлoжкa */
#overlay {
	z-index:30; /* пoдлoжкa дoлжнa быть выше слoев элементoв сaйтa, нo ниже слoя мoдaльнoгo oкнa */
	position:fixed; /* всегдa перекрывaет весь сaйт */
	background-color:#000; /* чернaя */
	opacity:0.9; /* нo немнoгo прoзрaчнa */
	-moz-opacity:0.9; /* фикс прозрачности для старых браузеров */
	filter:alpha(opacity=90);
	width:100%; 
	height:100%; /* рaзмерoм вo весь экрaн */
	top:0; /* сверху и слевa 0, oбязaтельные свoйствa! */
	left:0;
	cursor:pointer;
	display:none; /* в oбычнoм сoстoянии её нет) */
}


.open_modal {
	width: 100%;
}


.modal_content {
	color: @white;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	height: 100%;
	text-align: center;
	h2 {
		vertical-align: middle;
		text-transform: uppercase;
	}
}


#modal_close {
	background: @white;
	position: absolute;
	width: 25px;
	height: 2px;
	 float: right;
	transform: rotate(45deg);
	transition: all 0.05s linear;
	// &:hover {
	// 	transform: rotate(0deg);
	// }


}

#modal_close:before {
	position: absolute;
	content: "";
	background: @white;
	width: 25px;
	height: 2px;
	transform: rotate(-90deg);
	width: 25px;
	height: 2px;
	transition: all 0.05s linear;
	// &:hover {
	// 	display: none;
	// }
}

.wrap_close{
	display: flex;
    cursor: pointer;
    position: relative;
    width: 100%;
    height: 30px;
    /* float: right; */
    /* display: inline-block; */
    /* text-align: right; */
    justify-content: flex-end;
    bottom: -9px;
		&:hover {
			#modal_close:before {
				transform: rotate(0deg);
			}
			#modal_close{
				transform: rotate(0deg);
			}
		}
}

@import "_media.less";